.about-me {
  width: 100%;
  display: flex;
  justify-content: center;
  background: #3a3053;
  align-items: center;
  height: 70vh;
}
.about-me-box {
  width: 62%;
}
.my-image-container {
  width: 50%;
  display: flex;
  justify-content: flex-end;
}
.my-image-container img {
  width: 20%;
  position: absolute;
}
.about-info {
  width: 100%;
  display: flex;
  align-items: center;
}
.line {
  width: 50%;
}
.my-image-container img:nth-last-of-type(1) {
  width: 70%;
  position: relative;
  margin-right: -10%;
}
.about-me-box h2 {
  color: #f4f4f4;
  font-family: "Fira Code", monospace;
  font-weight: 300;
  width: 80%;
}
.about-me-box h2 img {
  width: 20%;
  height: 11px;
}
.line h2 img {
  width: 180px;
  margin-left: 10px;
}
.line p {
  color: #f4f4f4;
  width: 70%;
  font-size: 16px;
  padding: 3% 0% 3% 0%;
  font-family: "Fira Code", monospace;
}
.about-me-box h2 span {
  color: #c3afff;
}

.line h4 {
  font-family: "Fira Code", monospace;
  font-weight: 500;
  color: #f4f4f4;
  font-size: 120%;
}

.technologies {
  width: 70%;
  display: flex;
  flex-wrap: wrap;
  padding: 0;
  height: auto;
}
.technologies li {
  width: 30%;
  list-style: none;
  font-size: 18px;
  font-family: "Fira Code", monospace;
  color: #c3afff;
  line-height: 1.8;
}
.technologies li::before {
  content: "▸";
  color: #c3afff;
  margin-right: 5px;
}
.my-image-container img:hover {
  background-color: rgba(240, 248, 255, 0);
  transition: all 0.2s linear;
}

.my-image-container img:hover + .my-image-container img:nth-last-of-type(1) {
  margin-left: 8%;
  transition: all 0.2s linear;
}

